import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faGamepad,
  faLaptopCode,
  faRobot,
} from "@fortawesome/free-solid-svg-icons";

export default function Work() {
  return (
    <div
      id="work"
      className="bg-tanris p-20 h-auto flex flex-col items-center justify-center"
    >
      <h1 className="text-5xl text-black">Work 💼</h1>
      <h1 className="text-xl p-5 text-black">What i do ?</h1>
      <div className="py-10 flex flex-wrap md:grid grid-cols-3 gap-8">
        <div className="p-2 flex flex-col items-center rounded overflow-hidden shadow-md bg-gradient-to-b from-yellow-200 to-orange-300">
          <h1 className="text-2xl font-semibold p-2 mt-5 text-black text-center">
            Artificial Intelligence
          </h1>
          <div className="bg-gray-900 w-32 h-32 mt-5 rounded-full flex justify-center items-center">
            <FontAwesomeIcon
              className="text-beige transition duration-400 hover:text-gray-100"
              icon={faRobot}
              size="3x"
            />
          </div>
          <p className="text-black p-10">
            It's one of the most interesting subjects this last years and my
            master's degree was all about it, we learned a lot of stuff like
            machine learning, deep learning... For my end of studies' project, I
            used bio-inspired algorithms to enhance the contrast of skin lesion
            image and create a CNN classifier for automatic diagnosis of
            melanoma skin lesions.
          </p>
        </div>

        <div className="p-2 flex flex-col items-center rounded overflow-hidden shadow-md bg-gradient-to-b from-yellow-200 to-orange-300">
          <h1 className="text-2xl font-semibold p-2 mt-5 text-black text-center">
            App Dev
          </h1>
          <div className="bg-gray-900 w-32 h-32 mt-5 rounded-full flex justify-center items-center">
            <FontAwesomeIcon
              className="text-beige transition duration-400 hover:text-gray-100"
              icon={faLaptopCode}
              size="3x"
            />
          </div>
          <p className="text-black p-10">
            I have created some Web, Mobile and Desktop applications during my
            studies and have an ease with the development. I experienced a lot
            of technologies and programming languages and i find it quite
            interesting, that's why I am always eager to learn more.
          </p>
        </div>

        <div className="p-2 flex flex-col items-center rounded overflow-hidden shadow-md bg-gradient-to-b from-yellow-200 to-orange-300">
          <h1 className="text-2xl font-semibold p-2 mt-5 text-black text-center">
            Game Dev
          </h1>
          <div className="bg-gray-900 w-32 h-32 mt-5 rounded-full flex justify-center items-center">
            <FontAwesomeIcon
              className="text-beige transition duration-400 hover:text-gray-100"
              icon={faGamepad}
              size="3x"
            />
          </div>
          <p className="text-black p-10">
            It's my passion and the purpose of why I studied computer science, I
            often wright game proposal and sometimes try to create prototypes of
            my ideas with Unity or GameMaker. I participated in a game jam and
            it was a good experience, I hope to participate in more soon
          </p>
        </div>
      </div>
    </div>
  );
}
