import React from "react";

import javaLogo from "../image/skills/java.svg";
import pythonLogo from "../image/skills/python.svg";
import c4Logo from "../image/skills/c4.svg";
import reactLogo from "../image/skills/react.svg";
import angularLogo from "../image/skills/angular.svg";
import jsLogo from "../image/skills/javascript.svg";
import tsLogo from "../image/skills/typescript.svg";
import flaskLogo from "../image/skills/flask.svg";
import htmlLogo from "../image/skills/html.svg";
import cssLogo from "../image/skills/css.svg";
import firebLogo from "../image/skills/firebase.svg";
import unityLogo from "../image/skills/unity.svg";
import gameMLogo from "../image/skills/game-maker.svg";
import tfLogo from "../image/skills/tensorflow.svg";
import twLogo from "../image/skills/tailwind.svg";
import andLogo from "../image/skills/android.svg";



export default function Skills() {
  return (
    <div
      id="skills"
      className="bg-white p-20 h-auto flex flex-col justify-center items-center"
    >
      <h1 className="text-4xl text-black">Skills 👨‍💻</h1>
      <h1 className="text-xl text-black text-bold mt-10">
        Technologies I have worked with:
      </h1>
      <div className="py-10 flex flex-wrap md:grid grid-cols-8 gap-2">
  
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={reactLogo} alt="React"/>
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={angularLogo} alt="Angular"/>
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={jsLogo} alt="Javascript"/>
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={tsLogo} alt="Typescript"/>
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={htmlLogo} alt="Html"/>
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={cssLogo} alt="Css"/>
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={twLogo} alt="Tailwind"/>
        <img className="w-16 h-16 m-1 mb-3 ml-2 hover:animate-pulse" src={flaskLogo} alt="Flask"/>
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={firebLogo} alt="Firebase"/>
        <img className="w-16 h-16 m-1 mb-3 ml-2 hover:grayscale-0 hover:animate-pulse" src={pythonLogo} alt="Python"/>
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={tfLogo} alt="Tensorflow"/>
        <img className="w-16 h-16 m-1 mb-3 ml-2 hover:animate-pulse" src={javaLogo} alt="Java"/>
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={andLogo} alt="Android"/>
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={c4Logo} alt="C#"/>
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={unityLogo} alt="Unity"/>
        <img className="w-16 h-16 m-1 mb-3 hover:animate-pulse" src={gameMLogo} alt="GameMaker"/>

      </div>
       <h1 className="text-center">There are others, but I found it not relevant to display them all, if you are looking for a specific one ? Don't hesitate to ask me about 😉</h1>
    </div>
  );
}
