import React from "react";

import resumeFile from "../data/Resume Mokeddem-Abdelkader.pdf";

export default function Contact() {
  return (
    <div id="contact" className="bg-white p-20 flex flex-wrap justify-center">
      <h1 className="text-5xl text-black">Contact 📧</h1>
      <div className="p-5 flex flex-wrap justify-center items-start md:grid grid-cols-2 gap-8">
        <div className="p-5 flex flex-col justify-center items-center">
          <p className="text-black mt-5 text-lg text-center">
            If you have a request for a project or want to chat, <br/>
            feel free to email me 😄
          </p>
          <a href="mailto:mokeddemabdelkader07@gmail.com">
            <button className="p-4 bg-blue-600 rounded-lg font-bold text-white mt-5 transition duration-300 hover:bg-gray-600">
              Contact me ! 💬
            </button>
          </a>
        </div>

        <div className="p-5 flex flex-col justify-center items-center">
          <p className="text-black mt-5 text-lg text-center">
            For more information, please take a look at my resume 🕵️
          </p>
          <a href={resumeFile} target="_blank" rel="noopener noreferrer">
            <button className="p-4 bg-green-600 rounded-lg font-bold text-white mt-5 transition duration-300 hover:bg-gray-600">
              Grab a copy ! 📄
            </button>
          </a>
        </div>
      </div>
    </div>
  );
}
