import React from "react";
import { Link } from "react-scroll";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleDown } from "@fortawesome/free-solid-svg-icons";

function Home() {
  return (
    <div className="p-20 h-screen flex flex-col items-center justify-center ">
      <h1 className="text-5xl text-gray-200 ml-5 mt-20">Hello 👋</h1>
      <p className="ml-15 text-gray-500 mt-5 text-lg text-center">
        My name is <span className="text-white">Abdelkader Mokeddem</span>, I'm a
        computer scientist
      </p>
      <Link to="about" spy={true} smooth={true}>
        <FontAwesomeIcon
          className="static mt-24 animate-bounce text-gray-500 hover:text-white"
          icon={faAngleDown}
          size="3x"
        />
      </Link>
    </div>
  );
}

export default Home;
