import React from "react";
import { useEffect,useState } from "react";
import { Link } from "react-scroll";

function NavBar() {
  const [show, setShow] = useState(false)
  const controlNavbar = () =>{
    if(window.scrollY<100){
      setShow(false)
    }else{
      setShow(true)
    }
  }

  useEffect(()=> {
    window.addEventListener('scroll', controlNavbar)
    return () =>
    window.removeEventListener('scroll', controlNavbar)
  },[])

  return (
    <header className={`md:sticky top-0 flex justify-between transition duration-300 text-black ${show && `text-gray-300 bg-black opacity-90`}`} >
      <div className="flex items-center p-5">
        <span className={`title-font font-medium text-xl md:mb-0 ml-3 ${show && ` text-white`}`}>
          Abdelkader Mokeddem
        </span>
      </div>
      <div className={`flex items-center text-base ${show && `text-gray-300`}`}>
          <Link activeClass="active" to="home" spy={true} smooth={true}>
            <button className="mr-5 hover:text-white">Home</button>
          </Link>
          <Link to="about" spy={true} smooth={true}>
            <button className="mr-5 hover:text-white">About</button>
          </Link>
          <Link to="skills" spy={true} smooth={true}>
            <button className="mr-5 hover:text-white">Skills</button>
          </Link>
          <Link to="work" spy={true} smooth={true}>
            <button className="mr-5 hover:text-white">Work</button>
          </Link>
          <Link to="contact" spy={true} smooth={true}>
            <button className="mr-5 hover:text-white">Contact</button>
          </Link>
      </div>
    </header>
  );
}

export default NavBar;
