import React from "react";
import img_Me from "../image/img_Me.jpg";

export default function About() {
  return (
    <div
      id="about"
      className="bg-tanris p-20 h-auto flex flex-col items-center justify-center"
    >
      <h1 className="text-4xl text-black">About Me 👀</h1>
      <img
        className="w-32 h-32 rounded-full mx-auto mt-10"
        src={img_Me}
        alt="Me"
      />
      <p className="text-gray-900 md:px-56 mt-10 mb-10 text-lg text-center">
        I am a Computer Scientist, I have advanced knowledge in Artificial
        Intelligence. I like development and am interested in new technologies,
        I did some Web. Mobile and Desktop applications. Passionate about video
        games, all my time go playing or thinking about. I have a lot of
        game design ideas and game proposals, my aim is to create a very good one.
      </p>
    </div>
  );
}
